.ui-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  padding: 0 24px;
  .nav-btn {
    position: relative;
    .icon {
      width: 28px;
      aspect-ratio: 1/1;
      &.-profile {
        background: url("../../assets/images/icon/icon_nav_profile.svg") no-repeat 0 0;
        background-size: 100%;
        &.-active {
          background: url("../../assets/images/icon/icon_nav_profile_active.svg") no-repeat 0 0;
        }
      }
      &.-heart {
        background: url("../../assets/images/icon/icon_nav_heart.svg") no-repeat 0 0;
        background-size: 100%;
        &.-active {
          background: url("../../assets/images/icon/icon_nav_heart_active.svg") no-repeat 0 0;
        }
      }
      &.-meet {
        background: url("../../assets/images/icon/icon_nav_meet.svg") no-repeat 0 0;
        background-size: 100%;
        &.-active {
          background: url("../../assets/images/icon/icon_nav_meet_active.svg") no-repeat 0 0;
        }
      }
      &.-my {
        background: url("../../assets/images/icon/icon_nav_my.svg") no-repeat 0 0;
        background-size: 100%;
        &.-active {
          background: url("../../assets/images/icon/icon_nav_my_active.svg") no-repeat 0 0;
        }
      }
    }
    .title {
      margin-top: 4px;
      @include font(11px, var(--text2));
      font-weight: 700;
    }
    &.-update {
      &::after {
        content: "";
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background: var(--error);
        @include center(self, -4px, -4px, null, null);
      }
    }
  }
}
