.ui-watermark {
  z-index: var(--z-index-watermark);
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 691px;
  min-height: 100vh;
  background: rgba(0, 0, 0, 0.06);
  pointer-events: none;
  .mark-list {
    @include center(both);
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16vh 16vh;
    width: 100%;
    padding: 0 15px;
    & > li {
      flex-shrink: 0;
      width: 100%;
    }
    .mark-txt {
      display: flex;
      align-items: center;
      gap: 10px;
      width: 100%;
      opacity: 0.2;
      @include font(16px, var(--text9));
      font-weight: 500;
      transform: rotate(-35deg);
      & > * {
        flex-shrink: 0;
      }
    }
  }
}
