.ui-datepicker {
  width: 100%;
  padding: 12px 0 10px;
  border: 0;
  border-radius: 0;
  border-bottom: 1px solid var(--border-de);
  &.ant-picker-outlined {
    &:focus-within {
      outline: none;
      border-color: var(--border-de);
      box-shadow: none;
    }
    &:hover {
      border-color: var(--border-de);
    }
  }
  .ant-picker-input {
    & > input {
      @include font(16px, var(--text2), 1.375);
      font-weight: 500;
      font-family: var(--font-family-sans);
    }
  }
}

.datepicker-con{
	border-bottom: 1px solid var(--border-de);
	.ui-datepicker{
		border: 0;
	}
	&.-error {
    border-bottom: 1px solid var(--error);
  }
  &.-success {
    border-bottom: 1px solid var(--success);
  }
}

.ant-picker-dropdown {
  .ant-picker-cell {
    @include font(14px, var(--border-de));
    .ant-picker-cell-inner {
      width: 28px;
      height: 28px;
      line-height: 28px;
      border-radius: 50%;
    }
  }
  .ant-picker-cell-in-view {
    @include font(14px, var(--text2), 28px);
    font-weight: 500;
    &.ant-picker-cell-selected,
    &.ant-picker-cell-range-start,
    &.ant-picker-cell-range-end {
      .ant-picker-cell-inner {
        background: var(--success);
      }
    }
    &.ant-picker-cell-today {
      .ant-picker-cell-inner {
        &::before {
          border-radius: 50%;
          border-color: var(--primary);
        }
      }
    }
  }
  .ant-picker-content {
    th {
      @include font(14px, var(--text9));
      font-weight: 400;
    }
  }
  .ant-picker-today-btn {
    @include font(14px, var(--success));
    font-weight: 500;
  }
}
