.ui-container {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 691px;
  min-height: 100vh;
  margin: 0 auto;
  background-color: var(--white);
  &.-swiper {
    .ui-content {
      padding: 32px 0;
      .ui-list {
        padding: 0 24px;
      }
    }
  }
  &.-main {
    .wrap {
      flex: 1 0 0;
    }
  }
  &.-full {
    height: 100vh;
  }
  .ui-content {
    padding: 32px 24px;
    display: flex;
    flex-direction: column;
    flex: 1 0 0;
    gap: 16px;
    overflow: hidden;
    .img-set {
      &.-vh42 {
        height: 42vh;
        img {
          width: 100%;
          height: 100%;
					max-width: 320px;
          object-fit: scale-down;
        }
      }
    }
    .group-box {
      padding: 0 24px;
      .txt-group {
        padding: 30px 0;
        & > p {
          @include font(14px, var(--text2), 1.4);
          word-break: keep-all;
          & + p {
            margin-top: 15px;
          }
        }
      }
    }
    .notice-box {
      padding: 16px;
      border-radius: 16px;
      background: var(--bg-f7);
      .title {
        padding-bottom: 10px;
        border-bottom: 1px solid var(--border-de);
        @include font(14px, var(--text2), 1.4);
        font-weight: 700;
      }
      .chk-list {
        margin-top: 10px;
        & > li {
          display: flex;
          align-items: center;
          @include font(14px, var(--bg-4B), 1.4);
          &::before {
            content: "";
            flex-shrink: 0;
            width: 20px;
            height: 20px;
            margin-right: 5px;
            background: url("../../assets/images/icon/icon_check_small_gray.svg") no-repeat 0 / cover;
          }
          & + li {
            margin-top: 5px;
          }
        }
      }
    }
    &.-none-footer {
      padding: 32px 24px 60px;
    }
    &.-login {
      padding: 0;
    }
    &.-swiper {
      padding: 0 24px;
      margin-right: 24px;
      .swiper {
        overflow: visible;
        .swiper-wrapper {
          gap: 4px;
          .swiper-slide {
            @include font(14px, var(--text9), 1.42);
            letter-spacing: -0.5px;
            width: fit-content;
            padding: 7px 16px;
            border: 1px solid var(--border-de);
            border-radius: 999px;
            cursor: pointer;
            &.-active {
              border: 1px solid var(--success);
              color: var(--white);
              background-color: var(--success);
            }
          }
        }
      }
    }
    &.-qna {
      gap: 0;
      .content-top,
      .content-bottom {
        position: relative;
        padding: 12px 0 10px;
      }
      .content-top {
        border-bottom: 1px solid var(--border-de);
      }
    }
    &.-shadow {
      flex: 0;
      padding: 16px;
      border-radius: 16px;
      border: 1px solid var(--border-de);
      background: var(--white);
      box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.08);
    }
    &.-main-profile {
      max-width: 250px;
      margin-left: 24px;
      cursor: pointer;
    }
    &.-bg {
      position: relative;
      .bg-set {
        width: 100%;
        height: 100%;
        .tit-set {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          & > ul {
            & > li {
              font-size: 30px;
              font-weight: 700;
              line-height: 1.2;
            }
            & > li:nth-child(1) {
              color: var(--text2);
            }
            & > li:nth-child(2) {
              color: var(--text9);
            }
          }
        }
        & > img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
    }
    &.-mind {
      padding: 0 24px 32px;
      .ui-mindlist {
        .ui-grid {
          gap: 12px 24px;
        }
      }
      .ui-mindlist-item {
        cursor: pointer;
      }
    }
    &.-wantdate {
      padding: 0;
      .content-top {
        position: relative;
        .wrap {
          top: 0;
          left: 0;
          width: 100%;
        }
        .tit-set {
          position: relative;
          z-index: 1;
          width: 100%;
          padding: 25% 24px 25%;
        }
        .img-set {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
					max-width: 100%;
          filter: brightness(60%);
          & > img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            vertical-align: top;
          }
        }
      }
      .content-bottom {
        position: relative;
        background-color: var(--white);
        border-radius: 20px 20px 0 0;
        padding: 24px 24px 36px;
        .content:nth-child(2) {
          .price_guide {
            background: var(--bg-f7);
            padding: 14px 20px;
            border-radius: 12px;
          }
          .title {
            @include font(14px, var(--text5), 1.42);
            font-weight: 400;
          }
          .before_discount {
            & > div:nth-child(1) {
              @include font(16px, var(--text9), 1.375);
              font-weight: 700;
              text-decoration: line-through;
            }
            & > div:nth-child(2) {
              @include font(11px, var(--text9), 1.375);
              font-weight: 700;
            }
          }
          .after_discount {
            position: relative;
            & > div:nth-child(1) {
              @include font(30px, var(--success), 1.2);
              font-weight: 700;
            }
            & > div:nth-child(2) {
              @include font(11px, var(--error), 1.375);
              font-weight: 700;
            }
          }
          .after_discount::before {
            position: absolute;
            content: "";
            display: block;
            width: 1px;
            height: 100%;
            background-color: var(--border-de);
          }
        }
      }
    }
    &.-consent-terms {
      padding: 0;
      .entire-consent {
        padding: 16px;
        background: var(--bg-f7);
        border-radius: 16px;
        cursor: pointer;
      }
    }
  }
  .ui-footer {
    padding: 10px 24px;
    background: var(--white);
    @include safe-bottom(35px);
    z-index: var(--z-index-header);
    &.-login {
      background: var(--bg-f7);
      .footer-top {
        padding: 10px 0;
        border-bottom: 1px solid var(--border-de);
        & > .ui-icons {
          width: 40px;
          aspect-ratio: 48/37;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
        .evt-set {
          display: flex;
          align-items: center;
          gap: 10px;
          margin-top: 10px;
          .ui-btn {
            width: auto;
            padding: 0;
            @include font(14px, var(--text5));
            font-weight: 700;
          }
        }
      }
      .footer-content {
        padding: 15px 0;
        .info-list {
          & > li {
            @include font(12px, var(--text9), 1.4);
            font-weight: 500;
            &.flex {
              & > span {
                & + span {
                  display: flex;
                  align-items: center;
                  &::before {
                    content: "";
                    width: 1px;
                    height: 10px;
                    margin: 0 10px;
                    background: var(--border-de);
                  }
                }
              }
            }
            & + li {
              margin-top: 5px;
            }
          }
        }
      }
      .footer-bottom {
        padding: 10px 0;
        border-top: 1px solid var(--border-de);
        .menu-list {
          display: flex;
          align-items: center;
          // gap: 40px;
          & > li {
            .ui-btn {
              width: auto;
              padding: 0;
              @include font(12px, var(--text5));
            }
            & + li {
              display: flex;
              align-items: center;
              &::before {
                content: "";
                width: 1px;
                height: 10px;
                margin: 0 10px;
                background: var(--border-de);
              }
            }
          }
        }
      }
    }
    &.ui-sticky-bottom {
      position: sticky;
      bottom: 0;
      text-align: center;
      .cmt {
        @include font(13px, var(--text9));
        font-weight: 500;
      }
      .ui-btn {
        &.-txt {
          @include font(14px, var(--text5));
          font-weight: 500;
        }
      }
    }
    &.bottom-nav {
      border-top: 0.5px solid var(--border-de);
      box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.08);
    }
    &.-transparent {
      background: transparent;
    }
  }
  @media all and (max-width: 767px) {
    .ui-footer {
      padding: 6px 20px;
      @include safe-bottom(20px);
    }
  }
  @media all and (max-width: 320px) {
    .ui-content {
      padding: 30px 20px;
      .img-set {
        height: 38vh;
      }
    }
  }
}

.ui-dim {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--layer-dimmed);
  z-index: var(--z-index-dim);
}

.ui-grid {
  display: grid;
  &.-repeat2 {
    grid-template-columns: repeat(2, 1fr);
  }
  &.-repeat3 {
    grid-template-columns: repeat(3, 1fr);
  }
}
