.ui-btn {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  width: 100%;
  height: 56px;
  padding: 0 20px;
  border-radius: 56px;
  @include font(16px, var(--text2));
  .ui-icons {
    display: flex;
  }
  &.-update {
    &::after {
      content: "";
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background: var(--error);
      @include center(self, -4px, -4px, null, null);
    }
  }
  &.-primary {
    background: var(--btn-primary);
    color: var(--white);
    &:active {
      background: var(--btn-primary-active);
    }
  }
  &.-secondary {
    background: var(--btn-secondary);
    border: 1px solid var(--border-de);
    &:active {
      background: var(--btn-secondary-active);
    }
  }
  &.-tertiary {
    background: var(--btn-secondary);
    color: var(--text5);
    &:active {
      background: var(--btn-secondary-active);
    }
  }
  &.-danger {
    background: var(--btn-danger);
    color: var(--textf7);
    &:active {
      background: var(--btn-danger-active);
    }
  }
  &.-line {
    background: var(--btn-line);
    border: 1px solid var(--text2);
    &:active {
      background: var(--btn-line-active);
    }
  }
  &.-gray {
    background: var(--bg-f1);
    border-radius: 5px;
    &:active {
      background: var(--btn-line-active);
    }
  }
  &.-text {
    width: auto;
    height: 36px;
    padding: 0;
    justify-content: flex-start;
  }
  &.-kakao,
  &.-naver,
  &.-apple {
    gap: 15px;
    border-radius: 6px;
  }
  &.-kakao {
    background: var(--btn-kakao);
  }
  &.-naver {
    background: var(--btn-naver);
    color: var(--white);
  }
  &.-apple {
    background: var(--btn-apple);
    color: var(--white);
  }
  &.-dice {
    @include font(13px, var(--text2), 1.46);
    font-weight: 500;
    width: auto;
    border-radius: 8px;
    gap: 6px;
    padding: 0 12px !important;
    border: 1px solid var(--bg-f1);
    background: var(--bg-f1);
    &:active {
      border-color: var(--black);
    }
  }
  &.-mini {
    width: fit-content;
    height: fit-content;
    padding: 6px 12px;
    border-radius: 6px;
  }
  &.-bgc-1C88F4 {
    background: var(--secondary);
    color: var(--white);
  }
  &:disabled {
    background: var(--bg-f7);
    border: 0;
    color: var(--bg-b1);
    pointer-events: none;
  }
  &.-xs {
    height: 32px;
    font-size: 14px;
    .icon-loading {
      width: 16px;
    }
  }
  &.-sm {
    height: 36px;
    font-size: 14px;
    .icon-loading {
      width: 18px;
    }
  }
  &.-md {
    height: 40px;
    font-size: 14px;
    .icon-loading {
      width: 20px;
    }
  }
  &.-lg {
    height: 48px;
    font-size: 16px;
  }
  &.-xl {
    height: 56px;
    font-size: 16px;
  }
  &.-auto {
    width: auto !important;
    padding: 0 15px;
  }
  .icon-loading {
    width: 24px;
    aspect-ratio: 1/1;
    background: url("../../assets/images/icon/icon_loading.svg") no-repeat 0 0;
    background-size: 100%;
    animation: loading 0.5s linear infinite;
    &.-danger {
      background: url("../../assets/images/icon/icon_loading_danger.svg") no-repeat 0 0;
      background-size: 100%;
    }
  }
  @keyframes loading {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}

.ui-btn-icon {
  position: relative;
  display: inline-flex;
  gap: 10px;
  &.-update {
    &::after {
      content: "";
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background: var(--error);
      @include center(self, -4px, -4px, null, null);
    }
  }
  .ui-icons {
    width: 24px;
    aspect-ratio: 1/1;
  }
  &.-xs {
    .ui-icons {
    }
  }
  &.-sm {
  }
  &.-md {
    .ui-icons {
      width: 24px;
    }
  }
  &.-lg {
  }
  &.-xl {
  }
}

.ui-btn-group {
  position: relative;
  display: flex;
  align-items: center;
  margin-top: 10px;
  .btn-share {
    text-align: center;
    .ui-btn-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 64px;
      aspect-ratio: 1/1;
      border-radius: 50%;
      background: var(--text2);
      .ui-icons {
        width: 24px;
        aspect-ratio: 1/1;
      }
    }
    .tit {
      margin-top: 8px;
      @include font(14px, var(--text2));
      font-weight: 400;
    }
    &.-kakao {
      .ui-btn-icon {
        background: var(--yellow);
      }
    }
  }
}

.ui-btn-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 60px;
  width: 100%;
  padding: 10px 0;
  @include font(16px, var(--text2), 1.4);
  font-weight: 500;
  text-align: left;
  .placeholder {
    @include font(16px, var(--text9), 1.4);
  }
  .icon-set {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  .txt {
    margin-top: 5px;
    @include font(14px, var(--text5), 1.4);
    font-weight: 400;
  }
  &.-readonly {
    pointer-events: none;
  }
  &.-cursor {
    cursor: default;
  }
  &:disabled {
    opacity: 0.4;
    pointer-events: none;
  }
  &.-error {
    border-bottom: 1px solid var(--error);
  }
  &.-success {
    border-bottom: 1px solid var(--success);
  }
}
