:root {
  // colors
  --primary: #222222;
  --primary-active: color-mix(in oklab, var(--primary), white 16%);

  --main: #4d00b6;
  --secondary: #1c88f4;

  // --success: #33ddbc; 기존 컬러
	--success: #00C29D;
  --error: #ff4975;
  --warning: #ffdd33;
  --danger: #fef1ed;
  --reject: #ff6d90;
  --white: #ffffff;
  --white60: rgba(255, 255, 255, 0.6);
  --black: #000000;
  --yellow: #fee500;
  --green: #03c75a;

  --text2: #222222;
  --text2-rgba: rgba(34, 34, 34, 0.6);
  --text5: #505866;
  --text9: #9eaeb6;
  --textf7: #f74b20;

  --border-de: #dee8ee;
  --border-ea: #eaeff4;
  --border-cd: #cdd0e1;
  --border-e7: #e7e8f0;

  --bg-b1: #b1bbc0;
  --bg-f1: #f1f3f4;
  --bg-f7: #f7f8f9;
  --bg-f0: #f0f3f6;
  --bg-FFE: #ffeff3;
  --bg-FFF: #fff7ce;
	--bg-4B: #4B4B4B;
	--bg-33: #334155;
	--bg-F0: #F0F7FF;
	--bg-EB: #EBFFFB;

  --toast-info: #ffdd33;
  --toast-chk: #1c88f4;
  --toast-fail: #ffc8a9;
	--toast-danger: #f0523e;

  // button color
  --btn-primary: var(--primary);
  --btn-primary-active: color-mix(in oklab, var(--primary), var(--white) 16%);
  --btn-secondary: var(--white);
  --btn-secondary-active: color-mix(in oklab, var(--white), #000000 2%);
  --btn-tertiary: var(--bg-f1);
  --btn-tertiary-active: rgba(241, 243, 244, 0.84);
  --btn-danger: var(--danger);
  --btn-danger-active: color-mix(in oklab, var(--danger), #000000 2%);
  --btn-line: var(--white);
  --btn-line-active: color-mix(in oklab, var(--white), #000000 2%);
  --btn-kakao: var(--yellow);
  --btn-naver: var(--green);
  --btn-apple: var(--black);

  --layer-dimmed: rgba(0, 0, 0, 0.75);

  // semantic variables
  --placeholder: var(--text9);
  --placeholder-disabled: var(--bg-b1);

  // font
  --font-family-sans: "SUIT", sans-serif;

  // z-index
  --z-index-modal: 9999;
  --z-index-dim: 9998;
  --z-index-header: 9997;
  --z-index-tabs: 9997;
  --z-index-watermark: 9996;
}
