.chip {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  height: 24px;
  padding: 0 8px;
  border-radius: 8px;
  .img-set {
    width: 40px;
    aspect-ratio: 1/1;
    border-radius: 24px;
    & > img {
      width: 20px;
      height: 28px;
      object-fit: contain;
    }
  }
  .txt {
    @include font(16px, var(--text2), 1.37);
    font-weight: 700;
  }
  &.-label {
    padding: 4px 20px 4px 4px;
    flex-shrink: 0;
    .img-set {
      width: 32px;
      aspect-ratio: 1/1;
      border-radius: 24px;
    }
  }
  &.-text {
    width: fit-content;
    @include font(12px, var(--white), 1.33);
    font-weight: 700;
  }
  &.-certify {
    background: var(--success);
    color: var(--white);
  }
  &.-option {
    padding: 6px 16px;
    border-radius: 28px;
    background: var(--bg-f7);
    @include font(14px, var(--text5));
    font-weight: 500;
  }
  &.-info {
    color: var(--success);
    background: #ebfffb;
    border: 1px solid #b4f2e6;
  }
  &.-dday {
    padding: 4px 16px;
    border-radius: 28px;
    background-color: var(--white);
    @include font(14px, var(--toast-chk), 1.42);
    font-weight: 700;
    border: 1px solid var(--toast-chk);
  }
  &.-normal {
    background: var(--bg-f7);
    color: var(--text5);
  }
  &.-waring {
    background: var(--bg-FFF);
    color: var(--text5);
  }
  &.-review {
    background: var(--bg-F0);
    color: var(--secondary);
  }
  &.-reject {
    background: var(--bg-FFE);
    color: var(--error);
  }
  &.-round {
    border-radius: 12px;
  }
}
