.ui-content-input {
  display: flex;
  flex-direction: column;
  &.-wrap {
    flex-wrap: wrap;
    flex-direction: row;
  }
  &.-split {
    .input-wrap {
      & > input {
        @include font(16px, var(--text2), 1.375);
        padding: 12px 0 10px;
      }
      &.-error {
        & > input {
          border-bottom: 1px solid var(--error);
        }
      }
      &.-success {
        & > input {
          border-bottom: 1px solid var(--success);
        }
      }
    }
  }
  &.-circle {
    .input-set {
      & > input {
        letter-spacing: 10px;
      }
      & > input::placeholder {
        letter-spacing: -0.5px;
      }
    }
  }
  .ui-tit {
    font-size: 14px;
    font-weight: 700;
    line-height: 1.42;
    letter-spacing: -0.5px;
  }
  // text
  .input-set {
    padding: 12px 0 10px;
    display: flex;
    justify-content: space-between;
    > input {
      width: 100%;
      font-size: 16px;
      font-weight: 500;
      line-height: 1.375;
      color: #222;
    }
    > input::placeholder {
      color: #9eaeb6;
    }
    .unit {
      @include font(16px, var(--text9), 1.37);
      font-weight: 500;
      flex-shrink: 0;
    }
    &.-success {
      border-bottom: 1px solid var(--success);
    }
    &.-error {
      border-bottom: 1px solid var(--error);
    }
  }
  .input-option {
    display: flex;
    justify-content: space-between;
    gap: 16px;
    .desc {
      @include font(11px, var(--text9), 1.36);
      font-weight: 500;
      letter-spacing: -0.5px;
      &.-success {
        color: var(--success);
      }
      &.-error {
        color: var(--error);
      }
    }
  }
}

.ui-input {
  input[type="file"] {
    overflow: hidden;
    position: absolute;
    clip: rect(0, 0, 0, 0);
  }
  &.-select {
    .input-set {
      padding: 12px 0 10px;
      cursor: pointer;
      & > input {
        cursor: pointer;
      }
    }
  }
  &.-register-image {
    position: relative;
    width: 100%;
    height: 100%;
    & > label {
      cursor: pointer;
      display: block;
      width: 100%;
      height: 100%;
      .img-set {
        width: 100%;
        height: 100%;
        border-radius: 8px;
        overflow: hidden;
        & > img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      .img-none {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        .desc {
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translateX(-50%);
          @include font(12px, var(--text5), 1.33);
          font-weight: 400;
        }
      }
    }
    &.-reject {
      & > label {
        .img-set {
          position: relative;
          &::before {
            content: "";
            @include center(def);
            width: 100%;
            height: 100%;
            border-radius: 8px;
            background: rgba(255, 73, 117, 0.2);
            border: 1px solid var(--reject);
          }
        }
      }
    }
  }
  &.-slct {
    display: flex;
    justify-content: space-between;
    padding: 12px 0 10px;
    width: 100%;
    cursor: pointer;
    input {
      cursor: pointer;
    }
    &.-error {
      border-bottom: 1px solid var(--error);
    }
    &.-success {
      border-bottom: 1px solid var(--success);
    }
  }
  &.-idnumber {
    position: relative;
    .front,
    .back {
      width: 45%;
      display: flex;
      gap: 4px;
      padding: 12px 0 10px;
      .circle {
        position: relative;
        width: 18px;
        aspect-ratio: 1/1;
        border: 1px solid var(--border-cd);
        background-color: var(--white);
        border-radius: 999px;
        overflow: hidden;
        &.-active {
          border: none;
          background-color: transparent;
        }
        &.-disabled {
          border: none;
          background: var(--bg-f7);
          & > input {
            cursor: default;
          }
        }
        & > input {
          @include font(16px, var(--text2), 1.375);
          position: absolute;
          top: 0;
          left: 0;
          text-align: center;
          width: 18px;
          height: 18px;
          font-weight: 500;
          background-color: transparent;
        }
      }
    }
    .dash {
      @include font(16px, var(--text9), 1.375);
      font-weight: 500;
    }
  }
}
// rdo
.ui-rdo-set {
  input[type="radio"] {
    overflow: hidden;
    position: absolute;
    clip: rect(0, 0, 0, 0);
  }
  &.-rectangle {
    & > label {
      cursor: pointer;
      width: 90px;
      height: 110px;
      padding: 12px 16px;
      border-radius: 70px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid var(--border-de);
    }
    & > input:checked + label {
      border-color: var(--success);
      color: var(--white);
      background-color: var(--success);
    }
  }
  &.-mini {
    & > label {
      display: flex;
      @include font(14px, var(--text9), 1.42);
      font-weight: 500;
      padding: 7px 16px;
      border-radius: 999px;
      border: 1px solid var(--border-de);
      cursor: pointer;
    }
    & > input:checked + label {
      color: var(--white);
      border-color: var(--success);
      background-color: var(--success);
    }
  }
  &.-round {
    & > input:checked + label {
      border: 1px solid var(--success);
      color: var(--white);
      background-color: var(--success);
      & > .label-sub {
        color: var(--white);
      }
    }
    & > label {
      @include font(16px, var(--primary), 1.37);
      font-weight: 500;
      display: block;
      width: 100%;
      padding: 16px;
      border-radius: 58px;
      border: 1px solid var(--border-de);
      cursor: pointer;
      text-align: center;
      & > .label-sub {
        @include font(11px, var(--text9), 1.36);
        margin-top: 2px;
      }
    }
  }
  &.-item {
    padding: 8px 0;
    & > input:checked + label {
      @include font(16px, var(--text2), 1.37);
    }
    & > input:checked + label::after {
      content: "";
      display: inline-block;
      background: url("../../assets/images/icon/icon_check_small.svg") no-repeat 100% / contain;
      width: 20px;
      height: 20px;
    }
    & > label {
      display: flex;
      justify-content: space-between;
      width: 100%;
      @include font(14px, var(--text5), 1.42);
      font-weight: 500;
      cursor: pointer;
    }
  }
  &.-quadrilateral {
    & > input:checked + label {
      color: var(--white);
      background: var(--success);
      & > .label-sub {
        color: var(--white);
      }
    }
    & > label {
      @include font(15px, var(--primary), 1.37);
      font-weight: 500;
      display: block;
      width: 100%;
      padding: 16px;
      border-radius: 16px;
      background: var(--bg-f7);
      cursor: pointer;
      & > .label-sub {
        @include font(11px, var(--text9), 1.36);
        margin-top: 2px;
      }
    }
  }
  &.-appointment {
    .content {
      display: block;
      border-radius: 16px;
      margin-top: 8px;
      padding: 16px;
      background: var(--bg-f7);
      cursor: pointer;
      .content-top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .day {
          @include font(16px, var(--text2), 1.37);
          font-weight: 700;
        }
        .time {
          @include font(14px, var(--text5), 1.42);
          font-weight: 400;
          margin-top: 4px;
        }
        .dday {
          @include font(14px, var(--toast-chk), 1.42);
          font-weight: 700;
          height: fit-content;
          width: fit-content;
          padding: 4px 16px;
          border: 1px solid var(--toast-chk);
          background-color: var(--white);
          border-radius: 28px;
        }
      }
      .content-bottom {
        margin-top: 16px;
        .place {
          @include font(16px, var(--text2), 1.37);
          font-weight: 500;
        }
        .addr {
          @include font(14px, var(--text5), 1.42);
          font-weight: 400;
          margin-top: 4px;
        }
      }
    }
    input:checked + label {
      background-color: var(--success);
      .content-top {
        .day,
        .time {
          color: var(--white);
        }
        .dday {
          border: 1px solid var(--success);
          color: var(--success);
        }
      }
      .content-bottom {
        .place,
        .addr {
          color: var(--white);
        }
      }
    }
  }
  @media all and (max-width: 767px) {
    &.-round {
      & > label {
        @include font(14px);
        padding: 10px 15px;
      }
    }
  }
}

// chk
