  input[type="checkbox"] {
    overflow: hidden;
    position: absolute;
    clip: rect(0, 0, 0, 0);
  }
  &.-round {
    & > input:checked + label {
      color: var(--white);
      border-color: var(--success);
      background-color: var(--success);
    }
    & > label {
      @include font(14px, var(--text9), 1.42);
      width: 100%;
      cursor: pointer;
      font-weight: 500;
      display: flex;
      padding: 16px;
      align-items: center;
      border-radius: 58px;
      border: 1px solid var(--border-de);
    }
  }
  &.-rectangle {
    & > label {
      @include font(16px, var(--text2), 1.37);
      width: 90px;
      height: 110px;
      padding: 12px 16px;
      border-radius: 70px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid var(--border-de);
      cursor: pointer;
    }
    & > input:checked + label {
      border-color: var(--success);
      color: var(--white);
      background-color: var(--success);
    }
  }
  &.-mini {
    & > label {
      display: flex;
      @include font(14px, var(--text9), 1.42);
      font-weight: 500;
      padding: 7px 16px;
      border-radius: 999px;
      border: 1px solid var(--border-de);
      cursor: pointer;
    }
    & > input:checked + label {
      color: var(--white);
      border-color: var(--success);
      background-color: var(--success);
    }
  }
  &.-item {
    padding: 8px 0;
    & > input:checked + label {
      @include font(16px, var(--text2), 1.37);
    }
    & > input:checked + label::after {
      content: "";
      display: inline-block;
      background: url("../../assets/images/icon/icon_check_small.svg") no-repeat 100% / contain;
      width: 20px;
      height: 20px;
    }
    & > label {
      display: flex;
      justify-content: space-between;
      width: 100%;
      @include font(14px, var(--text5), 1.42);
      font-weight: 500;
    }
  }
  &.-block {
    & > label {
      @include font(12px, var(--text2), 1.33);
      cursor: pointer;
      font-weight: 500;
      padding: 12px 16px;
      border-radius: 8px;
      border: 1px solid var(--border-de);
      display: block;
    }
    & > input:checked + label {
      color: var(--white);
      border-color: var(--success);
      background-color: var(--success);
    }
  }
  &.-circle {
    & > input:checked + label {
      &::before {
        background: url("../../assets/images/icon/icon_check_small.svg") no-repeat 100% / contain;
      }
    }
    & > input + label {
      cursor: pointer;
      &::before {
        content: "";
        flex-shrink: 0;
        width: 20px;
        height: 20px;
        background: url("../../assets/images/icon/icon_check_small_black.svg") no-repeat 100% / contain;
      }
    }
    & > label {
      display: flex;
      align-items: center;
      gap: 0 8px;
      width: 100%;
      padding: 0;
      @include font(14px, var(--text5), 1.3);
      font-weight: 400;
    }
  }
  @media all and (max-width: 767px) {
    & > label {
      padding: 10px 15px;
    }
  }
}

.ui-content .-mbti {
  .content-top {
    display: flex;
    justify-content: space-between;
    padding: 4px;
    gap: 36px;
    align-items: center;
    border-radius: 278px;
    background: var(--bg-f7);
    box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.08) inset;
    .ui-rdo-set {
      input {
        display: none;
      }
      input:checked + label {
        color: var(--white);
        border-radius: 28px;
        box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.2), -1px -1px 0px 0px rgba(0, 0, 0, 0.1) inset, 1px 1px 0px 0px rgba(255, 255, 255, 0.5) inset;
        border: none;
      }
      label {
        @include font(24px, var(--text9), 1.29);
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 700;
        width: 40px;
        height: 40px;
        padding: 8px 16px;
        border: none;
        cursor: pointer;
      }
      &.-mbti-i {
        input:checked + label {
          background-color: #009cbc;
        }
      }
      &.-mbti-e {
        input:checked + label {
          background-color: #fb9e40;
        }
      }
      &.-mbti-n {
        input:checked + label {
          background-color: #fbcc43;
        }
      }
      &.-mbti-s {
        input:checked + label {
          background-color: #159432;
        }
      }
      &.-mbti-f {
        input:checked + label {
          background-color: #e42313;
        }
      }
      &.-mbti-t {
        input:checked + label {
          background-color: #3472b6;
        }
      }
      &.-mbti-p {
        input:checked + label {
          background-color: #b1bc00;
        }
      }
      &.-mbti-j {
        input:checked + label {
          background-color: #903ea7;
        }
      }
    }
    .line {
      width: 60px;
      height: 2px;
      background-color: rgba(34, 34, 34, 0.2);
    }
  }
  .content-bottom {
    .mbti-tit {
      display: flex;
      justify-content: center;
      gap: 24px;
      padding: 0 74px;
      align-items: center;
      .txt {
        @include font(16px, var(--text5), 1.37);
        font-weight: 500;
        letter-spacing: -0.5px;
      }
      .dot-set {
        width: 24px;
        height: 24px;
        align-items: center;
        justify-content: center;
        display: flex;
        gap: 4px;
        .dot {
          width: 2px;
          aspect-ratio: 1/1;
          background-color: var(--border-cd);
          border-radius: 8px;
        }
      }
    }
  }
}
// textarea
.ui-textarea {
  position: relative;
  border-bottom: 1px solid var(--border-de);
  &.-error {
    border-bottom: 1px solid var(--error);
  }
  &.-success {
    border-bottom: 1px solid var(--success);
  }
  &.-error + .desc {
    color: var(--error);
  }
  &.-success + .desc {
    color: var(--success);
  }
  &.-input {
    padding: 12px 0 10px;
  }
  textarea {
    @include font(16px, var(--text2), 1.37);
    width: 100%;
    height: 100%;
    font-weight: 500;
    position: relative;
    resize: none;
    border: none;
    box-shadow: none;
  }
  .close-btn {
    width: 20px;
    height: 20px;
    aspect-ratio: 1/1;
    background: url("../../assets/images/icon/icon_close_gry_circle.svg") no-repeat 100% / contain;
  }
}
.ui-textarea + .desc {
  @include font(11px, var(--text9), 1.36);
  font-weight: 500;
}

// selectbox
.ui-select {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  border-bottom: 1px solid var(--border-de);
  select {
    width: 100%;
    padding: 12px 0 10px;
    @include font(16px, var(--text2));
    font-weight: 500;
    option {
      @include font(16px, var(--text2));
      font-weight: 500;
      &:disabled {
        color: var(--text9);
      }
    }
    &.-placeholder {
      color: var(--text9);
    }
  }
  &.-success {
    border-bottom: 1px solid var(--success);
  }
  &.-error {
    border-bottom: 1px solid var(--error);
  }
}
.ui-selcet-set {
  display: flex;
  gap: 15px;
}

// file
.ui-inp-file {
  input[type="file"] {
    overflow: hidden;
    position: absolute;
    clip: rect(0, 0, 0, 0);
  }
  label {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40px;
    padding: 0 20px;
    border-radius: 20px;
    background: var(--btn-primary);
    cursor: pointer;
    @include font(14px, var(--white));
    &:active {
      background: var(--btn-primary-active);
    }
  }
  &.-line {
    label {
      background: var(--btn-line);
      border: 1px solid var(--border-de);
      @include font(14px, var(--text2));
      font-weight: 700;
      &:active {
        background: var(--btn-line-active);
      }
    }
  }
  &.-sm {
    label {
      height: 36px;
    }
  }
  &.-lg {
    label {
      height: 48px;
      border-radius: 24px;
      font-size: 16px;
    }
  }
  &.-xl {
    label {
      height: 56px;
      border-radius: 30px;
      font-size: 16px;
    }
  }
}

.no-caret {
  caret-color: transparent;
}

.input-circle {
  width: 18px;
  height: 18px;
  box-sizing: border-box;
  @include font(16px, var(--text2), 1);
  font-weight: 500;
  border: 1px solid var(--border-cd);
  border-radius: 50%;
  text-align: center;
  &:focus,
  &.-active {
    border: 0;
  }
  &:disabled {
    cursor: default;
    border: 0;
    background: var(--bg-f7);
  }
}
