.ui-modal-root {
  z-index: var(--z-index-dim);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  .icon-loading {
    @include center(both);
    width: 32px;
    aspect-ratio: 1/1;
    background: url("../../assets/images/icon/icon_loading_spinner.svg") no-repeat 0 0;
    background-size: 100%;
    animation: loading 0.5s linear infinite;
  }
  @keyframes loading {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}

.ui-modal-mask {
  width: 100%;
  height: 100%;
  background: var(--layer-dimmed);
}

.ui-modal {
  @include center(both);
  width: 340px;
  max-width: 691px;
  .ui-modal-content {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 24px;
    border-radius: 20px;
    background: var(--white);
    .ui-modal-close {
      @include center(self, 20px, 20px, null, null);
      @include font(16px, var(--text2));
    }
    .ui-modal-header {
      margin-bottom: 10px;
      .ui-modal-title {
        @include font(18px, var(--text2));
        font-weight: 700;
        &.-sub {
          margin-top: 10px;
          @include font(16px, var(--text9));
          font-weight: 400;
        }
      }
    }
    .ui-modal-body {
      margin-top: 20px;
      padding: 8px 0;
      .pop-txt {
        & > p {
          @include font(14px, var(--text2), 1.6);
          word-break: keep-all;
          & > strong {
            &.-error {
              color: var(--error);
            }
          }
        }
      }
      .ui-pop-list {
        overflow-y: auto;
        width: 100%;
        max-height: 180px;
      }
    }
    .ui-modal-footer {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 10px;
      margin-top: 20px;
      .ui-btn {
        width: 100%;
      }
      &.-short {
        .ui-btn {
          width: 100px;
        }
      }
      &.-line {
        padding-top: 15px;
        border-top: 1px solid var(--border-de);
      }
    }
    @include safe-bottom(35px);
  }
  &.-bottom {
    top: auto;
    bottom: 0;
    transform: translateX(-50%);
    width: 100%;
    .ui-modal-content {
      border-radius: 20px 20px 0 0;
    }
  }
  &.-full {
    @include center(def);
    width: 100%;
    max-width: 100%;
    min-height: 100%;
    transform: none;
    .ui-modal-content {
      border-radius: 0;
      .ui-modal-body {
        overflow-x: hidden;
        overflow-y: auto;
        height: calc(100vh - 65px);
      }
    }
  }
}

.ui-modal-toast {
  position: fixed;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  width: 100%;
}

.ui-pop-guide {
  max-width: 691px;
  margin: 0 auto;
  .guide-info-box {
    width: 100%;
    max-width: 420px;
    margin: 30px auto 0;
    .title {
      margin-bottom: 12px;
      @include font(14px, var(--text2));
      font-weight: 700;
    }
    .thumb-list {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 10px;
      & > li {
        .txt {
          margin-top: 5px;
          @include font(12px, var(--text2), 1.4);
          text-align: center;
          font-weight: 700;
        }
      }
    }
    .info-txt {
      margin-top: 15px;
      & > li {
        display: flex;
        gap: 0 5px;
        @include font(14px, var(--text5), 1.4);
        &::before {
          content: "·";
          font-weight: 700;
        }
      }
    }
  }
}

// 약관 팝업
.ui-pop-terms {
  width: 100%;
  max-width: 691px;
  margin: 0 auto;
  .terms {
    margin-top: 40px;
    .info-txt {
      margin-top: 15px;
      @include font(12px, var(--text5), 1.4);
      font-weight: 300;
      word-break: keep-all;
    }
    .txt-set {
      .txt {
        @include font(14px, var(--text2), 1.6);
        &.-small {
          @include font(12px, var(--text5), 1.6);
          font-weight: 300;
        }
        &.-dot {
          display: flex;
          &::before {
            content: "";
            flex-shrink: 0;
            width: 2px;
            height: 2px;
            margin: 9px 5px 0 0;
            border-radius: 50%;
            background: var(--text2);
          }
        }
        p {
          & > strong {
            font-weight: 500;
            text-decoration: underline;
          }
        }
      }

      & + .txt-set {
        margin-top: 15px;
      }
    }
  }
}

.pop-account {
  .tit {
    @include font(14px, var(--text5), 1.4);
    font-weight: 500;
  }
  .ui-info-list {
    margin-top: 5px;
    @include font(14px, var(--text5), 1.4);
    font-weight: 500;
  }
}
