.ui-design {
  &.-profile-sample {
    margin: 0 40px;
    padding: 16px;
    border: 1px solid var(--border-de);
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.08);
    border-radius: 16px;
    .content-design {
      padding-bottom: 24px;
      .img-set {
        width: 100%;
        height: auto;
        overflow: hidden;
        & > img {
          border-radius: 8px;
          width: 100%;
          height: 100%;
          object-fit: contain
        }
      }
      .tit-set {
        .tit {
          @include font(14px,var(--text2),1.42);
          font-weight: 700;
          letter-spacing: -0.5px;
        }
        .txt {
          @include font(10px,var(--text2),1.4);
          font-weight: 400;
          word-break: keep-all;
        }
      }
    }
    .content-design + .content-design {
      padding-top: 24px;
      padding-bottom: 0;
      border-top: 1px solid var(--border-de);
    }
  }
  &.-self-camera {
    position: relative;
    width: 100%;
    aspect-ratio: 1/1;
    background: var(--text2);
    border-radius: 16px;
    overflow: hidden;
    & > img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .pose {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      & > img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
  }
  &.-flow {
    .flow {
      width: max-content;
      animation: flow 20s linear infinite;
      & > .chip {
				height: 40px;
				border-radius: 20px;
        margin-right: 16px;
      }
    }
    @keyframes flow {
      0% {
        transform: translateX(-10%);
      }
      100% {
        transform: translateX(-60%);
      }
    }
  }
  &.-main-profile-info {
    padding: 0 16px;
    .nickname {
      @include font(28px,var(--text2),1.1);
      font-weight: 700;
      word-wrap: normal;
    }
    .age {
      @include font(16px,var(--text9),1.375);
      .number {
        font-weight: 700;
      }
      .string {
        font-weight: 300;
      }
    }
    .mbti {
      @include font(10px,var(--text5));
      width: 32px;
      aspect-ratio: 1/1;
      border-radius: 32px;
      border: 1px dashed var(--text5);
    }
    .addr {
      @include font(14px,var(--text5),1.42);
      font-weight: 300;
    }
    .job {
      @include font(14px,var(--text2),1.42);
      font-weight: 500;
    }
    .line {
      width: 12px;
      height: 1px;
      transform: rotate(90deg);
      background: var(--border-de);
    }
    .salary {
      @include font(14px,var(--text2),1.42);
      font-weight: 700;
    }
    .option {
      .desc {
        @include font(11px,var(--bg-b1),1.36);
        font-weight: 500;
      }
    }
  }
  &.-price-guide {
    background-color: var(--success);
    border-radius: 12px;
    display: flex;
    justify-content: space-between;
    padding: 20px;
    gap: 8px;
    align-items: center;
    .title {
      @include font(14px,var(--white),1.42);
      font-weight: 400;
    }
    .price {
      @include font(30px,var(--white),1.2);
      font-weight: 700;
    }
  }
  &.-registered-card {
    padding: 16px;
    background: var(--bg-f7);
    border-radius: 16px;
  }
  &.-d-day {
    width: fit-content;
    padding: 4px 16px;
    border-radius: 28px;
    &.-soon {
      background-color: var(--white);
      border: 1px solid var(--toast-chk);
      .ui-list-item:nth-child(1){
        @include font(14px,var(--toast-chk),1.42);
        font-weight: 700;
      }
      .ui-list-item:nth-child(2){
        @include font(12px,var(--toast-chk),1.33);
        font-weight: 500;
      }
    }
    &.-met {
      background: var(--bg-F0);
      .ui-list-item:nth-child(1){
        @include font(14px,var(--toast-chk),1.42);
        font-weight: 700;
      }
      .ui-list-item:nth-child(2){
        @include font(12px,var(--toast-chk),1.33);
        font-weight: 500;
      }
    }
    &.-got {
			@include font(12px,var(--white),1.33);
      background: var(--success);
      font-weight: 700;
    }
    &.-sent {
			@include font(12px,var(--success),1.33);
			background: var(--bg-EB);
      font-weight: 700;
    }
    &.-continue {
      @include font(12px,var(--text5),1.33);
      font-weight: 500;
      background: var(--bg-f7);
    }
  }
  &.-overlap-image {
    padding: 16px 0 16px;
    display: flex;
    justify-content: center;
    .img-set {
      position: relative;
			max-width: unset;
			margin: 0;
      & > img {
        position: absolute;
      }
    }
    .img-set:nth-child(1){
      margin-right: -20px;
      & > img {
        object-fit: cover;
        border-radius: 0px 40px 130px 130px;
        right: 0;
        bottom: 0;
      }
    }
    .img-set:nth-child(2){
      & > img {
        border-radius: 40px 0px 130px 130px;
        top: 0;
        left: 0;
      }
    }
  }
}