.ui-address {
  .total {
    @include font(11px, var(--text9), 1.2);
    font-weight: 500;
  }
  .address-list {
    margin-top: 20px;
    & > li {
      & + li {
        margin-top: 8px;
      }
    }
  }
}

.ui-address-card {
  padding: 16px;
  border-radius: 16px;
  background: var(--bg-f7);
  .zipcode {
    @include font(16px, var(--text2));
    font-weight: 500;
  }
  .info-list {
    margin-top: 15px;
    & > li {
      display: flex;
      gap: 0 15px;
      .label {
        flex-shrink: 0;
        width: 50px;
        @include font(12px, var(--secondary));
      }
      .txt {
        width: 100%;
        @include font(14px, var(--text2), 1.4);
        word-break: break-all;
      }
      & + li {
        margin-top: 8px;
      }
    }
  }
  &.-clickable {
    cursor: pointer;
  }
  &.-empty {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100px;
    .empty-txt {
      @include font(14px, var(--bg-b1));
    }
  }
}
