.ui-tabs {
  &.-mind {
    display: flex;
    text-align: center;
    background: var(--border-ea);
    padding: 3px;
    border-radius: 8px;
    .tab {
      @include font(16px, var(--text9), 1.375);
      cursor: pointer;
      font-weight: 700;
      width: 100%;
      padding: 8px 10px;
      border-radius: 6px;
      background: var(--border-ea);
      &.-active {
        background-color: var(--white);
        color: var(--text2);
      }
    }
  }
}
