.ui-login {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: calc(100vh - 60px);
  padding: 20px;
  .login-visual {
    position: relative;
    overflow: hidden;
    display: flex;
    width: 100%;
    height: 100%;
    .img-pos {
      @include center(both);
      width: 100%;
      height: 80%;
      & > img {
        width: 100%;
        height: 100%;
        object-fit: scale-down;
      }
    }
  }
  .login-form {
    width: 100%;
    text-align: center;
    .title-set {
      .tit {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        & + .tit {
          margin-top: 10px;
        }
      }
      @include font(24px, var(--text2), 1);
      font-weight: 700;
    }
    .btn-group {
      width: 100%;
      margin-top: 20px;
      @include safe-bottom(15px);
      & > .ui-btn {
        display: flex;
        align-items: center;
        width: 100%;
        height: 38px;
        border-radius: 6px;
        @include font(16px);
        font-weight: 700;
        & + .ui-btn {
          margin-top: 10px;
        }
      }
    }
  }
}
