.ui-setting {
  .invite-set {
    .title-set {
      .tit {
        @include font(14px, var(--text2), 1.3);
        font-weight: 700;
      }
      .txt {
        margin-top: 5px;
        @include font(12px, var(--text5), 1.4);
        font-weight: 300;
        & > strong {
          font-weight: 700;
        }
      }
    }
    .invite-total {
      .tit {
        @include font(12px, var(--text5));
        font-weight: 300;
      }
      .num {
        display: flex;
        gap: 5px;
        margin-top: 8px;
        @include font(36px, var(--text9));
        font-weight: 300;
        & > strong {
          color: var(--success);
          font-weight: 700;
        }
      }
    }
    .round-box {
      padding: 20px 40px;
      border-radius: 16px;
      background: var(--bg-f7);
      .url {
        @include font(12px, var(--text5), 1.4);
        font-weight: 300;
        text-align: center;
        word-break: break-all;
      }
    }
    & + .invite-set {
      margin-top: 30px;
    }
  }
}
.account-info {
  display: flex;
  align-items: center;
  gap: 5px;
  flex-shrink: 0;
  .ui-icons {
    flex-shrink: 0;
  }
  .txt {
    @include font(13px, var(--text9), 1.4);
    font-weight: 500;
  }
}

.business-area {
  padding: 30px 0;
  .title {
    padding: 10px 0;
    @include font(14px, var(--text2), 1.4);
    font-weight: 700;
    border-bottom: 1px solid var(--border-de);
  }
  .info-list {
    padding: 20px 0;
    & > li {
      @include font(14px, var(--text2), 1.4);
      .txt {
        margin-top: 5px;
        font-weight: 700;
        line-height: 1;
        .inner-txt {
          display: flex;
          align-items: center;
          line-height: 1;
          & + .inner-txt {
            &::before {
              content: "";
              width: 1px;
              height: 10px;
              margin: 0 10px;
              background: var(--text2);
            }
          }
        }
      }
      & + li {
        margin-top: 20px;
      }
    }
  }
}

.reject-box {
  display: flex;
  gap: 10px;
  padding: 10px 20px;
  border-radius: 8px;
  background: var(--bg-FFE);
  text-align: left;
  @include font(14px, var(--reject), 1.4);
  .label {
    flex-shrink: 0;
    font-weight: 700;
  }
}

.ui-scan-image {
  width: 248px;
  aspect-ratio: 248/324;
  margin: 0 auto;
  background: url("../../assets/images/bg/bg_graduate_example.svg") no-repeat 0 / contain;
}

.ui-file-upload {
  text-align: center;
  .ui-inp-file {
    margin: 0 auto;
  }
  .upload-file {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 64px;
    aspect-ratio: 1/1;
    margin: 20px auto 0;
    border: 1px solid var(--border-de);
    border-radius: 8px;
    .ui-btn-icon {
      @include center(self, 5px, 5px, null, null);
    }
  }
}
