.ui-list-set {
  padding: 0 20px;
}
.ui-list-title {
  @include font(14px, var(--text2), 1.4);
  font-weight: 700;
}
.ui-list {
  .ui-list-item {
    @include font(12px, var(--text5), 1.3);
    font-weight: 300;

    &.-dot::before {
      content: "•";
      display: inline-block;
      margin: 0 4px 0 2px;
    }
  }
  &.-choose {
    .ui-list-item {
      padding: 20px 16px;
      border-radius: 16px;
      background: var(--bg-f7);
      cursor: pointer;
      .txt {
        @include font(16px, var(--text9), 1.37);
        font-weight: 500;
        letter-spacing: -0.5px;
        &.-pick {
          color: var(--text2);
        }
      }
      .icon {
        box-sizing: border-box;
        &.-circle {
          width: 20px;
          height: 20px;
          border-radius: 999px;
          border: 1px solid var(--text5);
        }
        &.-rectangle {
          width: 28px;
          height: 20px;
          border-radius: 20px;
          border: 1px solid var(--text2);
        }
        &.-point-color {
          background-color: var(--success);
          border: none;
        }
      }
    }
  }
  &.-question {
    .ui-list-item {
      @include font(16px, var(--text2), 1.37);
      font-weight: 500;
      padding: 16px 0;
      border-bottom: 1px solid var(--border-de);
      cursor: pointer;
      &.-active {
        color: var(--text9);
        pointer-events: none;
      }
      &.-active::after {
        content: "";
        display: inline-block;
        background: url("../../assets/images/icon/icon_check-small_white.svg") no-repeat 100% / contain;
        width: 20px;
        height: 20px;
      }
    }
  }
  &.-photo {
    .ui-list-item {
      position: relative;
      width: 100%;
      aspect-ratio: 1/1;
      border-radius: 8px;
      background: var(--bg-f7);
      &.-essential::after {
        @include font(12px, var(--white), 1.33);
        content: "필수";
        display: block;
        position: absolute;
        top: -4px;
        left: -2px;
        font-weight: 700;
        padding: 4px 8px;
        background-color: var(--success);
        border-radius: 8px;
      }
      .register {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 8px;
      }
    }
  }
  &.-depth {
    .ui-list-item {
      position: relative;
    }
    .ui-list-item:first-child {
      padding-left: 0 !important;
    }
    .ui-list-item:last-child {
      padding-right: 0 !important;
    }
    .ui-list-item + .ui-list-item::before {
      position: absolute;
      display: block;
      content: "";
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 1px;
      height: 60%;
      background-color: var(--border-de);
    }
  }
  &.-terms {
    display: flex;
    flex-direction: column;
    .ui-list-item {
      padding: 6px 0;
      // cursor: pointer;
    }
  }
  &.-review {
    .ui-list-item {
      .ui-input {
        pointer-events: none;
        & > label {
          .img-set {
            position: relative;
            &::before {
              content: "";
              @include center(def);
              width: 100%;
              height: 100%;
              border-radius: 8px;
              border: 0;
              background: rgba(240, 247, 255, 0.7);
            }
            .img-none {
              display: none;
            }
          }
        }
        .ui-btn-icon {
          display: none;
        }
      }
    }
  }
}

.ui-prompt {
  .list-item {
    padding: 20px 16px;
    border-radius: 16px;
    background: var(--bg-f7);
    cursor: pointer;
    .item-top {
      .title-set {
        width: calc(100% - 40px);
        @include font(16px, var(--text2), 1.4);
        font-weight: 500;
        .placeholder {
          color: var(--text9);
        }
      }
      .icon {
        flex-shrink: 0;
        &.-circle {
          width: 20px;
          height: 20px;
          border-radius: 50%;
          border: 1px solid var(--text5);
        }
        &.-rectangle {
          width: 28px;
          height: 20px;
          border-radius: 20px;
          border: 1px solid var(--text2);
        }
      }
    }
    .item-bottom {
      margin-top: 16px;
      @include font(14px, var(--text5), 1.4);
    }
    &.-default {
      cursor: default;
    }
    &.-reject {
      background: rgba(255, 73, 117, 0.2);
      border: 1px solid var(--reject);
    }
  }
  &.-review {
    .list-item {
      opacity: 0.4;
      border: 0;
      background: var(--bg-f7);
      cursor: default;
      .item-top {
        .icon {
          display: none;
        }
      }
    }
  }
}
