.ui-table {
  border-top: 1px solid var(--border-ea);
	border-bottom: 1px solid var(--border-ea);
  th {
    padding: 10px;
    border-top: 1px solid var(--border-cd);
    background: var(--bg-f7);
    @include font(12px, var(--text5), 1.4);
    font-weight: 700;
    text-align: left;
  }
  td {
    padding: 10px;
    border-left: 1px solid var(--border-ea);
		border-top: 1px solid var(--border-ea);
    background: var(--white);
		@include font(12px, var(--text2), 1.4);
    &:first-child {
      border-left: 0;
    }
    .txt-list {
      & > li {
        display: flex;
        @include font(12px, var(--text2), 1.4);
        font-weight: 400;
        word-break: keep-all;
        &::before {
          content: "·";
          flex-shrink: 0;
          margin-right: 5px;
          font-size: 16px;
          line-height: 1;
        }
        & + li {
          margin-top: 5px;
        }
      }
    }
		.num-set{
			text-indent: -1.2em;
			padding-left: 2em;
			&.-inner{
				margin: 5px 0;
				text-indent: -1em;
				padding-left: 1em;
			}
			& > .txt{
				@include font(12px, var(--text2), 1.4);
				& + .txt{
					margin-top: 4px;
				}
			}
		}
  }
}
