.tooltip {
  display: inline-flex;
  align-items: center;
  position: relative;
  padding: 6px 12px;
  margin: 10px 0;
  border-radius: 6px;
  background: var(--secondary);
  @include font(14px, var(--white), 1.2);
  white-space: pre-line;
  &::after {
    content: "";
    @include center(horizontal);
    bottom: -16px;
    border-width: 8px;
    border-style: solid;
    border-color: var(--secondary) transparent transparent transparent;
  }
}
