.ui-switch {
  position: relative;
  display: inline-flex;
  flex-shrink: 0;
  width: 40px;
  height: 22px;
  & > input {
    display: none;
    &:checked {
      & + .handle {
        background: var(--success);
        &::before {
          transform: translate(18px, -50%);
        }
      }
    }
  }
  .handle {
    @include center(self, 0, 0, 0, 0);
    border-radius: 15px;
    background: var(--border-de);
    transition: all 0.4s;
    cursor: pointer;
    &::before {
      content: "";
      @include center(vertical);
      left: 5px;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background: var(--white);
      transition: all 0.4s;
    }
  }
}

.ui-switch-text {
  display: flex;
  justify-content: space-between;
  padding: 20px 0;
  .tit-set {
    .tit {
      @include font(16px, var(--text2), 1.4);
      font-weight: 500;
    }
    .txt {
      margin-top: 20px;
      @include font(12px, var(--text5), 1.4);
      font-weight: 300;
      white-space: pre-line;
    }
  }
  & + .ui-switch-text {
    border-top: 1px solid var(--border-de);
  }
}
