.ant-slider {
  .ant-slider-rail {
    background: var(--border-ea);
    box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.08) inset;
  }
  .ant-slider-track {
    background-color: var(--success);
  }
  &:hover {
    .ant-slider-track {
      background-color: var(--success);
    }
    .ant-slider-handle {
      &::after {
        box-shadow: 0 0 0 4px var(--success);
      }
    }
  }
  .ant-slider-handle {
    width: 24px;
    height: 24px;
    inset-block-start: -6px;
    &::before {
      content: none;
    }
    &::after {
      width: 24px;
      height: 24px;
      background: var(--white);
      box-shadow: 0 0 0 4px var(--success);
      inset-inline-start: 0;
      inset-block-start: 0;
    }
    &:hover {
      &::after {
        width: 24px;
        height: 24px;
        box-shadow: 0 0 0 4px var(--success);
        inset-inline-start: 0;
        inset-block-start: 0;
      }
    }
    &:focus {
      &::after {
        width: 24px;
        height: 24px;
        box-shadow: 0 0 0 4px var(--success);
        inset-inline-start: 0;
        inset-block-start: 0;
      }
    }
  }
}
