.ui-image {
  overflow: hidden;
  display: inline-flex;
  & > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &.-ellipse {
    width: 150px;
    aspect-ratio: 150/210;
    margin: 0 auto;
    border-radius: 110px;
    &.-success {
      border: 4px solid var(--success);
    }
    &.-error {
      border: 4px solid var(--reject);
    }
  }
  &.-rectangle {
    border-radius: 8px;
  }
  &.-face {
    border-radius: 0px 40px 140px 140px;
    width: 100%;
    aspect-ratio: 250/310;
  }
  &.-blur {
    & > img {
      filter: blur(4px);
    }
  }
}

.img-set {
	max-width: 320px;
	margin: 0 auto;
	img {
		width: 100%;
		height: 100%;
		object-fit: scale-down;
	}
	&.-vh42 {
		height: 42vh;
		img {
			width: 100%;
			height: 100%;
			object-fit: scale-down;
		}
	}
}
