// 가이드 페이지
.guide-wrap {
  padding: 10px;
  .guide-title-set {
    display: flex;
    align-items: center;
    gap: 0 15px;
    .guide-title {
      @include font(22px);
    }
    .guide-txt {
      @include font(18px, #1c88f4);
      text-decoration: underline;
    }
  }
}
.guide-component {
  padding: 20px;
  .guide-lnb {
    position: fixed;
    top: 20px;
    flex-shrink: 0;
    width: 200px;
    height: calc(100vh - 40px);
    padding-right: 2px;
    border-right: 1px solid #ddd;
    .cmt {
      @include font(14px, #888);
    }
    &-list {
      overflow-y: auto;
      width: 100%;
      height: calc(100% - 40px);
      margin-top: 15px;
      padding-right: 10px;
      & > li {
        & > a {
          display: block;
          padding: 5px;
          @include font(14px);
          &:hover,
					&.active {
            background: #33ddbc;
            color: #fff;
          }
        }
        & + li {
          margin-top: 5px;
        }
      }
      &::-webkit-scrollbar {
        width: 3px;
        height: 10px;
      }
      &::-webkit-scrollbar-track {
        background-color: #ddd;
      }
      &::-webkit-scrollbar-thumb {
        background-color: #33ddbc;
      }
    }
  }
  .guide-content {
    padding-left: 220px;
    .guide-dvs {
      padding: 10px;
      border: 1px solid #ddd;
      & > .title {
        border-bottom: 1px solid #ddd;
        padding-bottom: 10px;
        margin-bottom: 10px;
        @include font(20px);
        font-weight: 700;
      }
      .guide-set {
				position: relative;
        .inner-box {
          padding: 10px;
          border: 1px dashed #ddd;
          & > .title {
            border-bottom: 1px solid #ddd;
            padding-bottom: 10px;
            @include font(14px);
          }
					.cont {
            padding: 10px;
            &.row {
              display: grid;
              grid-template-columns: repeat(3, 1fr);
              gap: 10px 15px;
            }
						&.block{
							display: flex;
							flex-direction: column;
							gap: 5px;
						}
						&.flex-row{
							display: flex;
							flex-direction: row;
							gap: 15px;
						}
          }
					& + .inner-box{
						margin-top: 10px;
					}
        }
				.cont {
					display: flex;
					flex-direction: column;
					gap: 10px;
					padding: 10px;
				}
        &.grid-row {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          gap: 10px;
					.inner-box{
						margin-top: 0;
					}
        }
				&.flex-wrap{
					display: flex;
					align-items: center;
					flex-wrap: wrap;
					gap: 40px;
				}
      }
      & + .guide-dvs {
        margin-top: 30px;
      }
    }
  }
}

.guide-table {
	table-layout: fixed;
	border-collapse: collapse;
	width: 100%;
	margin: 10px 0;
	border-bottom: 1px solid #ccc;
	tr {
		&.line {
			border-top: 2px solid #ccc;
		}
	}
	thead {
		th {
			padding: 7px 5px;
			border: 1px solid #ccc;
			border-bottom: double #ccc;
			white-space: nowrap;
			background: #d9d9d9;
			@include font(14px);
		}
	}
	tbody {
		th,
		td {
			padding: 8px;
			border: 1px solid #ccc;
			@include font(13px, #555);
			text-align: left;
		}
		td {
			& > a {
				display: inline-block;
				color: #1c88f4;
				text-decoration: underline;
			}
			&.ac {
				text-align: center;
			}
			.yet,
			.ing,
			.hold,
			.end {
				display: inline-flex;
				align-items: center;
				justify-content: center;
				flex-shrink: 0;
				width: 70px;
				height: 24px;
				border-radius: 15px;
				@include font(12px, #fff);
				&::before {
					content: "";
					width: 100%;
					text-align: center;
				}
			}
			.yet {
				background: #eaeaea;
				color: #333;
				&::before {
					content: "작업전";
				}
			}
			.ing {
				background: #1c88f4;
				&::before {
					content: "작업중";
				}
			}
			.hold {
				background: #b5b5b5;
				&::before {
					content: "작업대기";
				}
			}
			.end {
				background: #33ddbc;
				&::before {
					content: "작업완료";
				}
			}
		}
	}
}
