.ui-steps {
  .step {
    position: relative;
    display: flex;
    padding: 10px 0 0;
    .step-num {
      display: flex;
      flex-direction: column;
      flex-shrink: 0;
      align-items: center;
      width: 32px;
      gap: 6px;
      &::after {
        content: "";
        width: 2px;
        height: 100%;
        background: url("../../assets/images/bg/bg_dot_line.png") repeat-y 0 / contain;
      }
      &.-last {
        &::after {
          content: none;
        }
      }
    }
    .step-wrap {
      .title-set {
        margin-top: 4px;
        .title {
          @include font(18px, var(--text2), 1.33);
          strong {
            font-weight: 700;
          }
        }
      }
      .txt-set {
        padding-bottom: 20px;
        min-height: 28px;
        .text {
          @include font(14px, var(--text5), 1.42);
          font-weight: 400;
        }
        .info-txt {
          margin-top: 10px;
          @include font(12px, var(--text5), 1.4);
          font-weight: 300;
        }
        .info-list {
          margin-top: 20px;
          & > li {
            .label {
              display: flex;
              align-items: center;
              margin-bottom: 12px;
              @include font(14px, var(--text5));
              font-weight: 300;
              &::before {
                content: "";
                width: 3px;
                height: 3px;
                margin-right: 5px;
                border-radius: 50%;
                background: var(--text2);
              }
            }
            .btn-grid {
              display: grid;
              grid-template-columns: repeat(2, 1fr);
              gap: 12px;
            }
            .doc-list {
              & > li {
                display: flex;
                gap: 15px;
                .chip {
                  flex-shrink: 0;
                }
                .tit-set {
                  .tit {
                    @include font(16px, var(--text2), 1.4);
                    font-weight: 500;
                  }
                  .cmt {
                    margin-top: 5px;
                    @include font(12px, var(--text5), 1.4);
                  }
                }
                & + li {
                  margin-top: 15px;
                }
              }
            }
            & + li {
              margin-top: 24px;
            }
          }
        }
      }
    }
  }
  &.-mind {
    .step {
      .step-wrap {
        .title-set {
          .title {
            font-weight: 700;
          }
        }
      }
    }
  }
}
