.ui-terms {
	.terms-set{
		.tit{
			margin-bottom: 5px;
			@include font(16px, var(--text2), 1.6);
		}
		.txt {
      @include font(14px, var(--text2), 1.6);
      &.-small {
        @include font(12px, var(--text5), 1.6);
        font-weight: 300;
      }
      &.-dot {
        display: flex;
        &::before {
          content: "";
          flex-shrink: 0;
          width: 2px;
          height: 2px;
          margin: 9px 5px 0 0;
          border-radius: 50%;
          background: var(--text2);
        }
      }
      p {
        & > strong {
          font-weight: 500;
          text-decoration: underline;
        }
      }
			& > a{
				color: var(--secondary);
			}
    }
		.num-set{
			text-indent: -1.2em;
			padding-left: 2em;
			&.-inner{
				margin: 5px 0;
				text-indent: -1em;
				padding-left: 1em;
			}
		}
		& + .terms-set{
			margin-top: 20px;
		}
	}
  .info-txt {
    margin-top: 15px;
    @include font(12px, var(--text5), 1.4);
    font-weight: 300;
    word-break: keep-all;
  }
