.ui-info {
  .nickname {
    @include font(28px, var(--text2), 1.1);
    font-weight: 700;
  }
  .age {
    @include font(16px, var(--text9), 1.37);
    font-weight: 300;
    .number {
      font-weight: 700;
    }
  }
  .mbti {
    @include font(10px, var(--text5), 1);
    padding: 4px 8px;
    width: 32px;
    aspect-ratio: 1/1;
    border-radius: 100%;
    border: 1px dashed var(--text5);
  }
  .tit-set {
    .tit {
      @include font(14px, var(--text5), 1.42);
      font-weight: 300;
    }
    .txt {
      @include font(16px, var(--text2), 1.37);
      font-weight: 500;
    }
    .description {
      @include font(12px, var(--text5), 1.33);
      font-weight: 300;
    }
  }
}
