.ui-title + .ui-title {
  margin-top: 16px;
}
.ui-title {
  @include font(24px, var(--text2), 1.3);
  font-weight: 300;
  &.-sub {
    font-weight: 700;
    @include font(16px, var(--text9), 1.3);
    & > strong {
      color: var(--text2);
    }
  }
  &.-emoji {
    @include font(18px, var(--text2), 1.3);
    font-weight: 700;
  }
  &.-large {
    @include font(30px, var(--text2), 1.2);
    font-weight: 700;
    text-align: center;
  }
  &.-help {
    @include font(12px, var(--text9), 1.33);
    font-weight: 400;
  }
  &.-caution {
    @include font(12px, var(--text9), 1.33);
    font-weight: 500;
  }
  &.-content-title {
    @include font(14px, var(--text2), 1.42);
    font-weight: 700;
  }
  &.-mindlist {
    .name {
      @include font(16px, var(--text2), 1.3);
      font-weight: 700;
    }
    .age {
      @include font(14px, var(--text9), 1.3);
      font-weight: 500;
    }
  }
  &.-advice {
    @include font(14px, var(--text9), 1.33);
    font-weight: 300;
  }
  &.-entire-consent {
    @include font(16px, var(--text2), 1.375);
    font-weight: 500;
  }
  &.-terms {
    @include font(14px, var(--text5), 1.42);
    font-weight: 400;
  }
  &.-question {
    @include font(14px, var(--text5), 1.42);
    font-weight: 300;
  }
  @media all and (max-width: 320px) {
    font-size: 20px;
    &.-sub {
      font-size: 14px;
    }
    &.-large {
      font-size: 24px;
    }
  }
}
.ui-title-set {
  & > .txt {
    margin-top: 16px;
    @include font(14px, var(--text9), 1.4);
    font-weight: 400;
    word-break: keep-all;
  }
  &.-sub {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 24px;
    .title-area {
      .title {
        @include font(18px, var(--text2), 1.4);
        font-weight: 700;
      }
      .txt {
        margin-top: 5px;
        @include font(14px, var(--text5), 1.4);
        font-weight: 400;
      }
    }
    .ui-select {
      width: 100px;
    }
    .ui-btn-list {
      width: 100px;
      min-height: auto;
      padding: 0;
      border: none;
      .tit {
        @include font(16px, var(--text5));
        font-weight: 500;
      }
    }
  }
}
.ui-txt-set {
  .ui-txt-list {
    margin-top: 20px;
  }
  .ui-txt {
    margin-top: 15px;
  }
  & > .txt {
    @include font(14px, var(--text2), 1.4);
    font-weight: 400;
    word-break: keep-all;
  }
}

.ui-join-unable {
  flex: 1 0 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  .ui-title-set {
    margin-bottom: 28px;
    text-align: center;
  }
  .ui-title {
    &.-sub {
      text-align: center;
    }
  }
  .join-date-box {
    border-top: 1px solid var(--border-de);
    padding-top: 28px;
    text-align: center;
    & > .title {
      @include font(16px, var(--success), 1.3);
      font-weight: 700;
    }
    & > .date-box {
      margin-top: 8px;
      @include font(36px, var(--text9), 1.3);
      letter-spacing: -0.5px;
      & > strong {
        color: var(--text2);
      }
			.date{
				@include font(36px, var(--text9), 1.3);
				& > strong {
					color: var(--text2);
				}
			}
			.time{
				@include font(36px, var(--text9), 1.3);
				font-weight: 300;
			}
    }
  }
  @media all and (max-width: 320px) {
    .join-date-box {
      & > .date-box {
        font-size: 30px;
      }
    }
  }
}

.ui-txt {
  @include font(12px, var(--text5), 1.6);
  font-weight: 300;
}
.ui-txt-list {
  @include font(14px, var(--text2), 1.6);
  font-weight: 400;
}

.ui-info-list {
  @include font(12px, var(--text5), 1.6);
  font-weight: 300;
  & > li {
    display: flex;
    &::before {
      content: "";
      flex-shrink: 0;
      width: 2px;
      height: 2px;
      margin: 9px 5px 0 0;
      border-radius: 50%;
      background: var(--text2);
    }
  }
}
