.ui-meeting {
  &.-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  &.-info {
    display: flex;
    gap: 21px;
    align-items: center;
    cursor: pointer;
    .img-set {
      flex-shrink: 0;
    }
    .tit-set {
      width: 100%;
      .nameage {
        .ui-list-item:nth-child(1) {
          @include font(16px, var(--text2), 1.375);
          font-weight: 700;
        }
        .ui-list-item:nth-child(2) {
          @include font(14px, var(--text9), 1.42);
          font-weight: 500;
        }
      }
      .addr {
        @include font(14px, var(--text5), 1.42);
        font-weight: 300;
      }
      .jobsalary {
        .ui-list-item {
          @include font(14px, var(--text2), 1.42);
          font-weight: 400;
        }
      }
      .job,
      .salary {
        @include font(14px, var(--text5), 1.42);
        font-weight: 300;
      }
      .date {
        @include font(12px, var(--text5), 1.33);
        font-weight: 300;
      }
    }
  }
  &.-schedule {
    .title {
      @include font(14px, var(--text5), 1.42);
      font-weight: 500;
    }
  }
}

.ui-adjustment-guide {
  padding: 0 24px 32px;
  text-align: center;
  .title {
    @include font(18px, var(--text2), 1.33);
    font-weight: 500;
  }
  .text {
    @include font(13px, var(--text9), 1.46);
    font-weight: 500;
    margin-top: 12px;
  }
  button {
    display: flex;
    margin: 16px auto 0;
    .title {
      @include font(14px, var(--text5), 1.42);
      font-weight: 500;
    }
  }
}

.ui-map {
  &.-card {
    padding: 16px;
    background: var(--bg-f7);
    border-radius: 16px;
    .addr {
      @include font(16px, var(--text2), 1.375);
      font-weight: 500;
    }
    button {
      @include font(13px, var(--white), 1.46);
      font-weight: 500;
      display: block;
      margin: 0 0 0 auto;
    }
  }
}

.ui-guide {
  .text {
    @include font(12px, var(--text5), 1.33);
    font-weight: 300;
  }
  button {
    @include font(14px, var(--text5), 1.42);
    font-weight: 500;
  }
}
