.ui-toast {
  z-index: var(--z-index-modal);
  position: absolute;
  left: 50%;
  bottom: 90px;
  transform: translateX(-50%);
  width: 100%;
  max-width: 80%;
  pointer-events: none;
  .toast-wrap {
    text-align: center;
    .toast-content {
      display: inline-flex;
      align-items: center;
      gap: 0 3px;
      border-radius: 6px;
      padding: 5px 12px 5px 8px;
      pointer-events: all;
      .icon {
        flex-shrink: 0;
        width: 18px;
        aspect-ratio: 1/1;
      }
      .txt {
        @include font(14px, var(--text2-rgba), 1.4);
        text-align: left;
        word-break: keep-all;
        white-space: pre-line;
      }
    }
  }
  &.-chk {
    .toast-wrap {
      .toast-content {
        gap: 0 5px;
        background: var(--toast-chk);
        .txt {
          color: var(--white);
        }
        .icon {
          background: url("../../assets/images/icon/icon_toast_chk.svg") no-repeat 0 0;
          background-size: 100%;
        }
      }
    }
  }
  &.-info {
    .toast-content {
      background: var(--toast-info);
      .icon {
        background: url("../../assets/images/icon/icon_toast_info.svg") no-repeat 0 0;
        background-size: 100%;
      }
    }
  }
  &.-fail {
    .toast-content {
      background: var(--toast-fail);
      .icon {
        background: url("../../assets/images/icon/icon_toast_fail.svg") no-repeat 0 0;
        background-size: 100%;
      }
    }
  }
  &.-danger {
    .toast-content {
      background: var(--toast-danger);
      .txt {
        color: var(--white);
      }
    }
  }
  &.-top {
    position: fixed;
    top: 100px;
  }
  &.-bottom {
    position: fixed;
    bottom: 100px;
  }
  @media all and (max-width: 767px) {
    bottom: 70px;
  }
}
