.ui-header {
  z-index: var(--z-index-header);
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  padding: 0 24px;
  background: var(--white);
  .ui-btn {
    padding: 0;
    .txt {
      @include font(12px, var(--text2));
    }
  }
  .header-left {
    display: flex;
    align-items: center;
    @include font(24px);
    font-weight: 700;
  }
  .header-center {
    @include center(horizontal);
  }
  .header-right {
    display: flex;
    align-items: center;
    gap: 0 12px;
  }
  &.-fixed {
    position: fixed;
    width: 100%;
    max-width: 691px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  &.-absolute {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0%;
  }
  &.-relative {
    position: relative;
  }
}
