.ui-notification {
  padding: 8px 16px;
  display: flex;
  gap: 6px;
  align-items: center;
  border-radius: 8px;
  background: var(--bg-f7);
  .tit-set {
    @include font(14px, var(--text9), 1.42);
    font-weight: 700;
    flex: 1 0 0;
    & > strong {
      color: var(--text2);
    }
  }
}
